"use client";
import React from "react";
import { FaUsers } from "react-icons/fa";
import { Group } from "../types"; // Import the Group type from the shared file

interface GroupSelectorProps {
  groups: Group[];
  selectedGroup: string;
  onSelectGroup: (groupId: string) => void;
}

const GroupSelector: React.FC<GroupSelectorProps> = ({ groups, selectedGroup, onSelectGroup }) => {
  return (
    <div className="flex items-center gap-3 bg-gray-50 p-4 rounded-lg shadow-sm">
      <FaUsers className="text-gray-600" />
      <select
        value={selectedGroup}
        onChange={(e) => onSelectGroup(e.target.value)}
        className="p-2 border rounded w-full focus:ring-2 focus:ring-blue-500"
      >
        <option value="">Sélectionner un groupe</option>
        {groups.map((group) => (
          <option key={group.idGroupe} value={group.idGroupe}>
            {group.nomGroupe} ({group.nbEtudiants} étudiants)
          </option>
        ))}
      </select>
    </div>
  );
};

export default GroupSelector;
