"use client";
import React from "react";
import { FaBuilding } from "react-icons/fa";

interface Room {
  id: string;
  name: string;
}

interface RoomSelectorProps {
  rooms: Room[];
  selectedRoom: string;
  onSelectRoom: (roomId: string) => void;
}

const RoomSelector: React.FC<RoomSelectorProps> = ({ rooms, selectedRoom, onSelectRoom }) => {
  return (
    <div className="flex items-center gap-3 bg-gray-50 p-4 rounded-lg shadow-sm">
      <FaBuilding className="text-gray-600" />
      <select
        value={selectedRoom}
        onChange={(e) => onSelectRoom(e.target.value)}
        className="p-2 border rounded w-full focus:ring-2 focus:ring-blue-500"
      >
        <option value="">Sélectionner une salle</option>
        {rooms.map((room) => (
          <option key={room.id} value={room.id}>
            {room.name}
          </option>
        ))}
      </select>
    </div>
  );
};

export default RoomSelector;
